
body {
  min-height: 100vh;
  height: 100%;
  overflow: hidden;
}
.full-width {
  width: 100%;
}
.eighty-width {
  width: 80%;
}
.wrap-text {
  word-wrap: break-word;
  overflow-wrap: break-word;
  max-width: 700px;
}
.mdl-mini-footer {
  padding: 8px 32px;
  min-height: 40px;
}
.no-records-chip {
  background-color: red;
}
.no-records-chip-text {
  font-size: 28px;
  color: #fff;
}
.no-records-chip-label {
  color: red;
}
.no-records-value-text {
  font-size: 24px;
  font-style: italic;
  text-align: center;
  background-color: rgba(0,0,0,.1)
}
.no-records-td {
  background-color: #000;
}
#no-records-ttl {
  background: #000;
  font-weight: bolder;
  color: red;
}
@media screen and (max-width: 960px) {
#dns-wrapper {
    max-width: 900px;
}
}



